import "./recipes.css";
import { DataGrid } from '@mui/x-data-grid';
import { Outlet } from "react-router-dom";
import { FormControl, Grid, TextField, InputLabel,Container,Typography } from "@material-ui/core";
import Button from '@material-ui/core/Button';
import { useState, useEffect } from "react";
import axios from "axios"
import { Link } from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete'; 
export default function ProductList() {


  const [orders, setOrder] = useState([]);
  const [loading, setLoading] = useState(true);


  const handleDelete = (id) => {
    setOrder(orders.filter((item) => item.id !== id));
    axios.delete("https://glitchhub.coffee/api/v1/recipes/"+id)
  };

  useEffect( () => {

    const fetchData = async () => {
      const result = await axios(
        "https://glitchhub.coffee/api/v1/recipes",
      );

      setOrder(result.data);
      setLoading(false)
    };
    fetchData();

  });

  const buttonE = () =>{
    return (<h1>test</h1>)
  }




  const columns = [
    { field: "id", headerName: "Product", width: 400,editable: true },
    {
      field: "edit", 
      headerName:"Edit",
      renderCell:(params)=>{
        return(
          <Link
          to={`/recipe/${params.row._id}`}
          key={params.row._id}
        >
          <Button style={{backgroundColor: '#12824C', color: '#FFFFFF'}}
          variant="contained" className="productListEdit">Edit</Button>
          </Link>
        )
      }
    },
    {
      field: "action",
      headerName: "Delete",
      width: 150,
      renderCell: (params) => {
        return (
          <>
            <DeleteIcon
              className="productListDelete"
              onClick={() => handleDelete(params.row.id)}
            />
          </>
        );
      },
    },
    { field: "Create", 
    width: 250,
    editable: true, 
    headerName:           
      <Link  to={`/recipe/newRecipe`}>
        <div> 
        <Button
              className=""
              color="primary" 
              variant="contained">
              Create Recipe
            </Button>
        </div>
      </Link>
      },

  ];

  return (
    
    <div className="productList">

      {/* <ul>
        {orders.map(order => (
          <li key={order.id}>
            <p>{order.id}, {order.date}</p>
          </li>
        ))}
      </ul> */}
        
        <DataGrid
          loading={loading}
          rows={orders}
          disableSelectionOnClick
          columns={columns}
          pageSize={20}
        />

    </div>
  );
}
